<template>
  <div class="login">
    <div class="center">
      <div class="logo"></div>
      <van-form>
        <van-field
          v-model="phone"
          name="phone"
          label="用户名"
          placeholder="用户名"
          :rules="[{ required: true, message: '请填写用户名' }]"
        />
        <van-field
          v-model="password"
          type="password"
          name="password"
          label="密码"
          placeholder="密码"
          :rules="[{ required: true, message: '请填写密码' }]"
        />
        <div style="margin: 16px">
          <van-button round block type="info" @click="onSubmit"
            >登录</van-button
          >
        </div>
        <div class="registered" @click="registered">注册</div>
      </van-form>
    </div>
  </div>
</template>

<script>
import { Toast } from 'vant';
export default {
  data () {
    return {
      phone: '',
      password: '',
    }
  },
  methods: {
    onSubmit () {
      console.log('执行')
      let data = {
        phone: this.phone,
        password: this.password
      }
      this.api.login(data).then(res => {
        if (res.data.code == 200) {
          console.log(res)
        } else {
          Toast(res.data.message)
        }
      }).catch(err => {
        console.log(err)
      })
    },
    registered () {
      this.$router.push('/registered')
    }
  },
}
</script>

<style lang="less" scoped>
.login {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: url("../../assets/img/bg.png") no-repeat center;
  background-size: 100% 100%;
  object-fit: fill;
  .logo {
    width: 100px;
    height: 100px;
    background: url("../../assets/img/logo.svg") no-repeat center;
    background-size: 180% 180%;
    position: absolute;
    top: 20%;
    left: 50%;
    margin-left: -50px;
    border-radius: 50%;
  }
  .center {
    background: #fff;
    border-radius: 8px;

    /deep/ .van-form {
      padding: 12px 0;
    }

    /deep/ .van-cell {
      background: rgba(0, 0, 0, 0);
    }

    /deep/ .van-field__control {
      background-color: rgba(0, 0, 0, 0) !important;
    }

    .registered {
      font-size: 14px;
      color: royalblue;
    }
  }
}
</style>